import React, { Fragment, Component } from 'react';
import {
  Card,
  Row,
  Col,
  InputNumber,
  Input,
  // Form,
  message,
  Table,
  Upload,
  Button,
  Icon,
  Modal,
} from 'antd';
import moment from 'moment';
import { CartHeadAction, TableDataEditCell, DatePickerString } from 'components/common';
import { BtnExport, BtnEdit, BtnAddNew } from 'components/common/button';
import { isEmpty } from 'lodash';
// import { requiredRule } from 'utils/validateRules';
import Page from 'components/LayoutComponents/Page';
import { getQueryParams } from 'utils/urlUtil';
import { getClassOwnerActivitys, saveClassOwnerActivity, exportClassOwnerActivities } from 'services/classOwnerActivity';
import SearchBar from './SearchBar';
import './style.scss';

class ClassOwnerActivity extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
      loading: false,
      paramSearch: {},
      updatedClasses: {},
      tableData: {},
      enableExport: false,
      dataKLCVforPopup: null,
    };
    this.columns = [
      {
        title: 'Khóa',
        dataIndex: 'courseLabel',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'studyFieldLabel',
      },
      {
        title: 'Năm học',
        dataIndex: 'yearEduLabel',
      },
      {
        title: 'Học kỳ',
        dataIndex: 'semesterLabel',
      },
      {
        title: 'Lớp',
        dataIndex: 'classLabel',
      },
      {
        title: 'Tên giáo viên',
        dataIndex: 'teacherName',
      },
      {
        title: 'Ngày sinh hoạt cuối cùng',
        dataIndex: 'lastActivityDate',
        render: (text, record) => <span>{record.lastActivityDate && moment(record.lastActivityDate).format('DD-MM-YYYY')}</span>,
      },
      {
        title: 'Số ngày',
        dataIndex: 'noOfDay',
        key: 'noOfDay',
        render: (text, record) => <span>{record.noOfDay}</span>,
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <BtnEdit onClick={this.handClickTableEdit(record)} />
          </Fragment>
        )
      },
    ];
    this.cssBetweenRow = { marginBottom: '8px' };
    this.columnsPopup = [
      {
        title: 'Ngày sinh hoạt',
        dataIndex: 'activityDate',
        render: (text, record) => (
          <span>
            <DatePickerString 
              value={record.activityDate} 
              placeholder="Chọn ngày"
              onChange={(date) => this.handleChangeActivityDate(date,record.id)}
            />
          </span>
        ),
      },
      {
        title: 'Khối lượng (ngày)',
        dataIndex: 'noOfDay',
        render: (text, record) => (
          <Fragment>
            {<InputNumber value={record.noOfDay} onChange={(value) => this.handleChangeNoOfDay(value, record.id)} />}
          </Fragment>
        ),
      },
      {
        title: 'Nội dung',
        dataIndex: 'activityContent',
        render: (text, record) => (
          <Fragment>
            {<Input value={record.activityContent} onChange={(value) => this.handleChangeContent(value,record.id)} />}
          </Fragment>
        ),
      },
      {
        title: 'File',
        dataIndex: 'attachment',
        render: (text, record) => {
          let customProps = {};
          if (record.newFile) {
            customProps = {
              fileList:[{
                uid: record.id,
                name: record.newFile.name,
                status: "done",
              }],
            }
          } else if (record.attachment) {
            customProps = {
              fileList:[{
                uid: record.id,
                name: record.attachment,
                status: "done",
              }],
            }
          }
          return (
            <Upload
              name="file"
              listType="text"
              onRemove={this.handleRemoveFile}
              beforeUpload={() => false}
              onChange={(uploadData) => this.handleUploadFile(uploadData, record.id)}
              showUploadList={{
                showRemoveIcon: false,
                showDownloadIcon: false,
              }}
              {...customProps}
            >
              <Button>
                <Icon type="upload" /> Upload
              </Button>
            </Upload>
          );
        },
      },
    ];
  }  

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  setValueToKey = (value, id, key) => {
    console.log('value',value);
    const dataTemp = {...this.state.dataKLCVforPopup};
    const foundIndex = dataTemp.classOwnerActivities.findIndex(x => x.id === id);
    dataTemp.classOwnerActivities[foundIndex][key] = value;
    this.setState({dataKLCVforPopup: dataTemp});
  }

  handleExport = async() => {
    try {
      await exportClassOwnerActivities(this.state.paramSearch);
      message.success('Xuất dữ liệu thành công');
    } catch(err) {
      message.error('Có lỗi xãy ra!');
    }
  }

  handleSave = async() => {
    const { updatedClasses, reload } = this.state;
    try {
      if(!isEmpty(updatedClasses)) {
        const classes = Object.entries(updatedClasses).map(classEntry => ({
          classId: classEntry[1].classId,
          teacherId: classEntry[1].teacherId,
          yearEduId: classEntry[1].yearEduId,
          semesterId: classEntry[1].semesterId,
          noOfDay: classEntry[1].noOfDay,
        }));
        await saveClassOwnerActivity(classes);
        message.success('Cập nhật thành công');
        this.setState({
          reload: reload + 1,
          updatedClasses: {}
        })
      }
    } catch(error) {
      message.error('Có lỗi xãy ra');
    }
  }

  handleSaveCellData = newItem => {
    const { updatedClasses, tableData } = this.state
    const identifyKey = `${newItem.classId}_${newItem.teacherId}_${newItem.yearEduId}_${newItem.semesterId}`;
    const oldItem = tableData[identifyKey];
    if(oldItem.noOfDay !== newItem.noOfDay) {
      updatedClasses[identifyKey] = newItem;
      this.setState({ updatedClasses });
    } else if(updatedClasses[identifyKey]) {
      delete updatedClasses[identifyKey]
      this.setState({ updatedClasses });
    }
  }

  handClickTableEdit = record => {
    return () => this.setState({
      dataKLCVforPopup: JSON.parse(JSON.stringify(record)),
    });
  }

  handleChangeActivityDate = (value, id) => {
    this.setValueToKey(value, id, 'activityDate');
  }

  handleChangeNoOfDay = (value, id) => {
    this.setValueToKey(value, id, 'noOfDay');
  }
  
  handleChangeContent = (value, id) => {
    this.setValueToKey(value.target.value, id, 'activityContent');
  }

  handleUploadFile = ({ file }, id) => {
    this.setValueToKey(file, id, 'newFile');
  }

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handClickAddNewCer = () => {
    const tempData = {...this.state.dataKLCVforPopup};
    tempData.classOwnerActivities.push({
      id: `NEW-${Math.random()}`,
      activityDate: new Date(),
      noOfDay: 1,
      activityContent: 'Không có',
      attachment: null,
    });
    this.setState({dataKLCVforPopup: tempData});
  }

  handleOKPopup = async() => {
    console.log('ok popup');
    console.log(this.state.dataKLCVforPopup);
    const patt = new RegExp("NEW");
    const { dataKLCVforPopup, reload } = this.state;
    try {
      if(dataKLCVforPopup) {
        const tempData = {...dataKLCVforPopup};
        const formData = new FormData();
        formData.append('classId', tempData.classId);
        formData.append('teacherId', tempData.teacherId);
        formData.append('yearEduId', tempData.yearEduId);
        formData.append('semesterId', tempData.semesterId);
        const tempActivities = [];
        tempData.classOwnerActivities.forEach(item => {
          const formatItem = {
            id: item.id,
            noOfDay: item.noOfDay,
            activityContent: item.activityContent,
            activityDate: item.activityDate,
            file: item.attachment,
            attachmentId: item.attachmentId,
          }
          if(patt.test(formatItem.id)) {
            delete formatItem.id;
          }
          if(item.newFile) {
            formatItem.tempId = item.newFile.name;
            formData.append('files', item.newFile, item.newFile.name);
          }
          tempActivities.push(formatItem);
        });
        formData.append('activitiesString', JSON.stringify(tempActivities));
        await saveClassOwnerActivity(formData);
        message.success('Cập nhật thành công');
        this.setState({
          reload: reload + 1,
          dataKLCVforPopup: null,
        })
      }
    } catch(error) {
      message.error('Có lỗi xãy ra');
    }
  }

  handleCancelPopup = () => {
    console.log('cancel popup', this.state.dataKLCVforPopup);
    this.setState({
      dataKLCVforPopup: null,
    })
  }

  handleRemoveFile = () => {
    console.log('handleRemoveFile');
  }

  parseArrayToObject = arrayData => {
    const result = {};
    arrayData.forEach(item => {
      result[`${item.classId}_${item.teacherId}_${item.yearEduId}_${item.semesterId}`] = item;
    });
    return result;
  }

  render() {
    const {
      loading,
      enableExport,
      paramSearch: { trainTypeId, facultyId, courseId, studyFieldId }
    } = this.state;
    
    return (
      <Card title='Khối lượng công việc cố vấn / chủ nhiệm' className="class-owner-activity">
        <Row>
          <CartHeadAction>
            <BtnExport
              title="Xuất dữ liệu"
              className="btn-export"
              disabled={!enableExport}
              onClick={this.handleExport}
            />
          </CartHeadAction>
        </Row>
        <Row gutter={16}>
          <Col span={24}>
            <SearchBar />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableDataEditCell
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={async(params) => {
                const result = await getClassOwnerActivitys(params);
                if(result.data && result.data.items) {
                  this.setState({
                    enableExport: !isEmpty(result.data.items),
                    tableData: this.parseArrayToObject(result.data.items)
                  })
                }
                return result;
              }}
              columns={this.columns}
              loading={loading}
              handleSaveCellData={this.handleSaveCellData}
              allowGetData={!!trainTypeId && !!facultyId && !!courseId && !!studyFieldId}
              rowKey="key" 
            />
          </Col>
        </Row>
        <Modal
          title={`KLCV của ${this.state.dataKLCVforPopup && this.state.dataKLCVforPopup.teacherName}`}
          visible={!!this.state.dataKLCVforPopup}
          onOk={this.handleOKPopup}
          okText="Lưu"
          onCancel={this.handleCancelPopup}
          cancelText="Hủy"
          width={800}
        >
          <Fragment>
            <Row>
              <BtnAddNew onClick={() => this.handClickAddNewCer()} />
            </Row>
            <Table
              columns={this.columnsPopup} 
              dataSource={this.state.dataKLCVforPopup && this.state.dataKLCVforPopup.classOwnerActivities}
              bordered
              rowKey="id"
            />
          </Fragment>
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassOwnerActivity />
  </Page>
);



